@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  src: local("Oswald"), url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  src: local("Oswald Medium"), url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  min-width: auto;
  margin: 0;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  color: #444444;
}
a {
  text-decoration: none;
  color: #111111;
}
img {
  max-width: 100%;
  height: auto;
}
iframe {
  display: block;
  width: 100%;
  height: 360px;
  border: none;
}
@media (min-width: 768px) {
  iframe {
    height: 400px;
  }
}
.title-section {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 400;
}
.title-block {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  text-transform: uppercase;
  color: #111111;
}
.title-content {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase;
  color: #111111;
}
.title-nav {
